@import url("https://fonts.googleapis.com/css?family=Mulish&display=swap");

.about-container {
  width: 100%;
  background-color: #14131a;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-row {
  margin-top: -15px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.profile-container {
  margin-top: 25px;
  width: auto;
}

#profile-img {
  margin: 10px 0;
  object-fit: cover;
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.img-card {
  margin: 0 5px;
  max-width: 550px;
  width: fit-content;
  background-color: #1c1b23;
  border: 1px solid #2d2d2d;
  padding: 10px 5px 10px 5px;
  border-radius: 15px;
  font-family: "Mulish", sans-serif;
  overflow: hidden;
}

.img-card h3,
.img-card h6 {
  color: white;
  padding: 5px;
}

.img-card span {
  background-color: white;
  color: black;
  margin-right: 5px;
  padding: 4px 7px;
  font-size: 13px;
  border-radius: 5px;
}

.img-card button {
  background-color: rgb(123, 123, 243);
  color: white;
  border: none;
  border-radius: 5px;
  margin: 5px;
  padding: 2px 17px;
}

.img-card button:hover {
  background-color: rgb(143, 143, 247);
}

.bio-container {
  width: 650px;
  min-width: 280px;
  margin: 0 5px;
}

.bio-container p {
  margin-top: 25px;
  padding-left: 10px;
  font-size: 17.5px;
  color: rgb(244, 241, 241);
  text-align: left;
}

.bio-container span {
  color: rgb(104, 154, 230);
  font-weight: 450;
  text-transform: capitalize;
}

.section-title {
  text-decoration: underline rgb(104, 154, 230) 2.5px;
  text-underline-offset: 10px;
  margin-left: 15px;
  font-weight: 500;
  color: white;
  text-align: left;
  font-size: 29px;
}

.edu-card,
.skill-card {
  color: white;
  background-color: #1c1b23;
  padding: 10px 5px 10px 20px;
  margin: 10px;
  border-radius: 15px;
}

.edu-card h4,
.skill-card h4 {
  font-size: 22px;
  font-weight: 700;
}

.edu-card h5,
.skill-card h5 {
  font-size: 18px;
  font-weight: 400;
}

.edu-card h6 {
  font-size: 15px;
  font-weight: 300;
}

.edu-card h6 span {
  float: right;
  padding-right: 20px;
  font-weight: 500;
}

.skill-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.skill-wrapper h5 {
  background-color: #14131a;
  border: 1px solid lightgray;
  font-size: 14px;
  font-weight: 400;
  padding: 7px 12px;
  border-radius: 5px;
  margin: 8px 8px 8px 0;
}

.skill-wrapper h5:nth-child(1) {
  border-color: rgb(234, 96, 96);
}

.skill-wrapper h5:nth-child(2) {
  border-color: slateblue;
}

.skill-wrapper h5:nth-child(3) {
  border-color: lightgreen;
}

.skill-wrapper h5:nth-child(4) {
  border-color: yellow;
}
